//Includes
	@import 'prefix';
	@import 'reset';
	@import 'normalize';
	@import '../../dist/icomoon/icomoon';
	@import 'loadawesome';
//Colors
	$b: #000;
	$w: #fff;
	$clear:transparent;
//Percentages
	$p1: 100%;
	$p2: 50%;
	$p3: 33.33333%;
	$p4: 25%;
	$p5: 20%;
	$p8: 12.5%;
	$p10: 10%;
// Styles

// Client

:root {
	--bk-bg-color: #fff;
	--bk-border-color: #e7e9ed;
	--bk-today-bg-color: #005d90;//#374d79
	--bk-today-color: #fff;
	--bk-selected-bg-color: #E7E9ED;
	--bk-selected-color: #333;
	--bk-base-color: #4b77ae;
	--bk-admin-base:#5183ca;
	--bk-admin-highlight:#60a0f3;
	--bk-admin-pending:#c78503;
}



#bk-med-wrapper {
	width:$p1;
	max-width:980px;
	margin:auto;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	opacity:1;
	color:#1a1a1a;
	* {
		@include box-sizing(border-box);
	}
	#bk-calendar,#bk-form,#bk-side-notice,.bk-med-status {
		@include transition(opacity .4s);
	}
	&.bk-loading {
		#bk-calendar,#bk-form,#bk-side-notice,.bk-med-status {
			opacity:0;
		}
		.bk-cal-loader-wrap {
			display:block;
		}
	}
	h1,h2,h3,h4,h5,h6 {
		text-transform: none;
		letter-spacing: 0;
	}
	button,input[type="submit"]{
		background:var(--bk-admin-base);
	}
	.bk-med-status {
		background: #ef0707;
	    border-radius: 4px;
	    margin: 0 0 12px;
	    font-size: 14px;
	    color: $w;
	    padding: 6px 14px;
	    font-weight: bold;
	    display: inline-block;
	    font-family: inherit;
	    &.bk-success {
	    	background:#06b60c;
	    }
	    p {
	    	margin:0;
	    	margin-bottom:0;
	    	color:$w;
	    }
	}
	.bk-cal-loader-wrap {
		position: absolute;
		top:0;
		left:0;
		width:$p1;
		height:$p1;
		// background:#f6f6f6;
		z-index:999;
		display:none;
	}
	.bk-cal-loader {
		color: var(--bk-admin-base);
		position: fixed;
		top: $p2;
		left: $p2;
		/* display: none; */
		z-index: 999999;
	    @include transform(translate(-$p2,-$p2));
	}
	#bk-side-notice {
		font-size:16px;
		background:var(--bk-admin-base);
		float:right;
		padding:20px 22px;
	    width: calc(33.33333% - 20px);
	    margin-right: 20px;
	    margin-bottom:10px;
	    display:inline-block;
		h1 {
			font-size:16px;
			font-family: inherit;
			margin:0;
			font-weight: bold;
			color:$w;
			&:before {
				display:none;
			}
		}
		p {
		    font-size: 13px;
		    font-family: inherit;
		    margin: 14px 0;
		    color: $w;
		    line-height: 1.4;
		}
		ul {
			padding-left:0;
			li {
				font-size: 13px;
    			line-height: 1.5;
				font-family: inherit;
				font-weight:bold;
				color:$w;
				list-style: none;
			}
		}
	}
	#bk-calendar {
		font-size: 0;
		margin:0;
		width: $p1;
		&.bk-hasside {
			max-width:calc(66.66666% - 20px);
			display:inline-block;
			.bk-cal{
				.bk-body {
					// overflow: hidden;
				}
				// &.bk-cal-prev {
				// 	.bk-date {
				// 		margin-left: -.5px;
				// 	    flex: 0 0 calc(14.28% + 2.5px);
				// 	    max-width: calc(14.28% + 2.5px);
				// 	}
				// }
				// &:not(.bk-cal-prev) {
				// 	.bk-date {
				// 		margin-right:-1.5px;
				// 	    flex: 0 0 calc(14.28% + 1.5px);
				// 	    max-width: calc(14.28% + 1.5px);
				// 	}
				// }
			}
		}
		.bk-cals {
			background-color: var(--bk-bg-color);
			border-radius: var(--bk-border-radius);
			border: solid 1px #ccc;
			box-shadow: 0 2px 6px 0 rgba(0,0,0,0.05);
		}
		.bk-cal {
			position:relative;
			display: inline-block;
		    width: $p2;
		    font-size: 14px;
		    vertical-align: top;
		    background:var(--bk-bg-color);
			*,*:before,*:after {
				@include box-sizing(border-box);
			}
			&.bk-cal-prev {
				&:after {
				    content: '';
				    position: absolute;
				    top: 0;
				    right: -1px;
				    height: $p1;
				    width: 1px;
				    z-index: 1;
					background-color:#ccc;
				}
			}
			&:not(.bk-cal-prev){
				margin-left:0;
			}
			.bk-btn {
				position: absolute;
				-moz-user-select: none;
				-ms-user-select: none;
				-webkit-appearance: button;
				background: none;
				border: 0;
				color: inherit;
				cursor: pointer;
				font: inherit;
				line-height: normal;
				min-width: 27px;
				outline: none;
				overflow: visible;
				padding: 0;
				text-align: center;
				left:10px;
				height:24px;
				&.bk-mobi-next {
					display:none;
				}
				&:active {
					border-radius: var(--bk-border-radius);
					box-shadow: 0 0 0 2px rgba(var(--bk-today-bg-color), 0.1)
				}
				&.next {
					left:auto;
					right:10px;
				}
			}
			.bk-header {
				align-items: center;
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				padding: 12px 14px;
				svg {
					fill: var(--bk-admin-base);
				}
			}

			.bk-header__label {
				font-weight: bold;
				text-align: center;
				width: $p1;
				line-height:1.4
			}

			.bk-week {
				background-color: var(--bk-selected-bg-color);
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				flex-wrap: wrap;
				border-bottom:1px solid #cccccc;
				span {
					flex-direction: column;
					flex: 0 0 14.28%;
					font-size: 11px;
					font-weight: bold;
					max-width: 14.28%;
					padding: 10px 10px;
					text-align: center;
					text-transform: uppercase;
				}
			}

			.bk-body {
				background-color: rgba(var(--bk-selected-bg-color), 0.3);
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				flex-wrap: wrap;
				margin-bottom:-1px;
				&[data-date-touches="true"]{
					.bk-date {
						&:last-child {
							flex: 0 0 calc(14.28% + 2px);
						    width: calc(14.28% + 2px);
						}
					}
				}
			}
			.bk-date {
				position:relative;
				align-items: center;
				background-color: #fff;
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				flex-direction: column;
				flex: 0 0 calc(14.28% + 2px);
				max-width: calc(14.28% + 2px);
				padding: 14px 0;
				border: 1px solid #ccc;
			    margin-right: -2px;
			    margin-top: -1px;
				&:last-child {
				    flex: 0 0 calc(14.28% + 1px);
				    width: calc(14.28% + 1px);
				}
			    &:before {
			    	display:none;
			    }
				span {
					line-height: normal;
				}
				&.haspop {
					z-index:99;
				}
				.bk-popup {
				    position: absolute;
				    bottom: 0px;
				    left: $p2;
				    will-change:transform;
				    color:$w;
				    background: $w;
				    border-top:2px solid var(--bk-admin-base);
				    @include box-shadow(0 1px 20px -2px rgba(0, 0, 0, 0.58));
				    border-radius: 2px;
					@include transform(translate(-$p2, $p1) scaleY(1));
					transform-origin: 0 0;
					@include transition(transform .15s ease-in-out);
					&:after {
						content:'';
						position: absolute;
						top:-2px;
						left:0;
						width:$p1;
						height:2px;
						background:rgba(0,0,0,.3);
					}
					&.bk-popfull {
						border-top:0;
						.bk-timeslot-info {
							margin: 0;
						    width: $p1;
						    border-bottom: 0;
						    h1 {
						    	width: $p1;
							    color: $w;
						    }
						}
					}
					&.bk-hidden {
						@include transform(translate(-$p2, $p1) scaleY(0));
					}
					&:before {
						// content:'';
						// position:absolute;
						// top:-6px;
						// left:$p2;
						// @include transform(translateX(-$p2));
						// width: 0; 
						// height: 0; 
						// border-left: 6px solid transparent;
						// border-right: 6px solid transparent;
						// border-bottom: 6px solid #335f8f;
					}
					.bk-timeslot-info {
						white-space: nowrap;
					    width: 60%;
					    border-bottom: 1px solid black;
					    margin: auto;
						h1 {
							text-align:center;
							font-size:14px;
							font-weight:bold;
							margin: 0;
	    					padding: 10px 8px;
	    					color:$b;
							&:before {
								display:none;
							}
						}
					}
					.bk-timeslot {
	    				font-size:14px;
	    				position: relative;
	    				background:$w;
	    				color:$b;
	    				padding:2px 6px;
	    				&:hover {
	    					background:#ededed;
	    				}
						input {
							position: absolute;
	    					visibility: hidden;
	    					&:checked ~ .time-checkbox {
	    						&:after {
	    							content:'';
	    							width:5px;
	    							height:5px;
	    							position:absolute;
	    							top:$p2;
	    							left:$p2;
	    							background:$b;
	    							border-radius:$p1;
	    							@include transform(translate(-$p2,-$p2));
	    						}
	    					}
						}
						label {
							cursor: pointer;
							position:relative;
							z-index:9;
							padding: 4px 8px 4px 26px;
							display: block;
							white-space: nowrap;
							font-weight:bold;
						}
						.time-checkbox {
						    width: 12px;
						    height: 12px;
						    border: 2px solid $b;
						    position: absolute;
						    left: 11px;
						    border-radius:$p1;
							top:$p2;
							@include transform(translateY(-$p2));
						}
					}
					.bk-timeslot-action {
				    	width: $p1;
					    padding: 6px 0 8px;
					    font-size: 11px;
					    background: #d0d0d0;
					    border-radius:0;
					    outline:0!important;
					    color:#a0a0a0;
					    cursor: not-allowed;
					    &.active {
					    	background: var(--bk-admin-base);
					    	color:$w;
					    	cursor: pointer;
					    }
					}
				}
				&.bk-date--booked {
					.bk-popup {
						background:$w;
						bottom:auto;
						top:-6px;
						@include transform(translate(-$p2, -$p1) scaleY(1));
						opacity:1;
						@include transition(opacity .15s ease-in-out);
						&:before {
							content:'';
							position:absolute;
							bottom:-6px;
							left:$p2;
							@include transform(translateX(-$p2));
							width: 0; 
							height: 0; 
							border-left: 6px solid transparent;
							border-right: 6px solid transparent;
							border-top: 6px solid #cc0033;
						}
						.bk-timeslot-info {
							h1 {
							    padding: 5px 8px 7px;
							    margin: 0;
							    background: #cc0033;
							}
						}
						&.bk-hidden {
							opacity:0;
							@include transform(translate(-$p2, -$p1) scaleY(1));
						}
					}
				}
				.bk-half {
					position: absolute;
				    top: 0;
				    left: 0;
				    width: $p1;
				    height: $p1;
				    overflow: hidden;
				    z-index:0;
				    .bk-rotate {
				    	width: 180%;
				    	height: 220%;
				    	position: absolute;
				    	top:$p2;
				    	left:$p2;
				    	@include transform(rotate(-26deg) translate(-$p2,-$p2));
				    	transform-origin: 0 0;
				    	.bk-half-first {
					    	height:$p2;
					    	width:$p1;
					    	top:0;
					    	// background:#5183ca;
					    	background:var(--bk-admin-base);
					    }
					    .bk-half-last {
					    	height:$p2;
					    	width:$p1;
					    	bottom:0;
					    	// background:#60a0f3;
					    	background:var(--bk-admin-highlight);
					    }
				    }
				}
				&.bk-date-pending {
					&.bk-date--booked {
						background:var(--bk-admin-pending);
					}
			    	.bk-half {
					    .bk-rotate {
					    	.bk-half-first {
						    	background:var(--bk-admin-pending);
						    }
						    .bk-half-last {
						    	background:var(--bk-admin-pending);
						    	opacity:.7;
						    }
					    }
					}
					&.bk-date--selected {
						.bk-half {
						    .bk-rotate {
						    	.bk-half-first {
							    	background:var(--bk-admin-pending);
							    }
							    .bk-half-last {
							    	background:var(--bk-admin-pending);
							    	opacity:1;
							    }
						    }
						}	
					}
			    }
				&.bk-date--selected {
					.bk-half {
					    .bk-rotate {
					    	.bk-half-first {
						    	background:#335f8f;
						    }
						    .bk-half-last {
						    	background:#335f8f;
						    }
					    }
					}
				}
				&.holiday {
					background:#32373c!important;
					color:white;
					.bk-date-span,.bk-half {
						display:none;
					}
					.bk-lg-icon {
						line-height:0;
						padding-top:2px;
					}
				}
			}
			&.bk-cal-prev {
				.bk-body {
					&[data-date-touches="true"]{
						.bk-date {
							&:last-child {
								flex: 0 0 calc(14.28% + 3px);
							    width: calc(14.28% + 3px);
							}
						}
					}
				}
				.bk-date {
					margin-left:-1px;
				    flex: 0 0 calc(14.28% + 3px);
					max-width: calc(14.28% + 3px);
					&:last-child {
					    flex: 0 0 calc(14.28% + 1px);
					    width: calc(14.28% + 1px);
					}
				}
			}

			.bk-date--active {
				cursor: pointer;
			}

			.bk-date--today {
				background-color: var(--bk-selected-bg-color);
				color: $b;
				position: relative;
				span {
					position: relative;
					z-index:9;
				}
				&:after {
					display:none;
					content:'';
					position:absolute;
					top:0;
					left:0;
					width:$p1;
					height:$p1;
					background:rgba(0,0,0,.3);
				}
			}

			.bk-date--selected {
				background-color: var(--bk-admin-base);
				color: $w;
				span {
					position: relative;
					z-index:9;
				}
				&:after {
					content:'';
					position:absolute;
					top:0;
					left:0;
					width:$p1;
					height:$p1;
					background:rgba(0,0,0,.3);
				}
			}

			.bk-date--disabled {
				border-radius: 0;
				cursor: not-allowed;
				span {
					opacity: 0.3;
				}
			}
			.bk-date--booked {
				background-color: var(--bk-admin-base);
				color: $w;
				span {
					opacity:1;
				}
			}
			.bk-date--half {
				color: $w;
				span {
					position: relative;
					z-index:1;
				}
			}
			.bk-hidden {
				visibility: hidden;
				opacity:1;
			}
		}
		.bk-selection {
		    font-size: 11px;
		    margin: 10px 1px;
		    font-weight: bold;
		    color: #333;
		    display: inline-block;
			&.chosen-time {
				float:right;
			}
		}	
		.bk-legend {
			display: -webkit-box;
			display: -moz-box;
			display: -ms-flexbox;
			display: -webkit-flex;
			display: flex;
			font-size:11px;
			flex-wrap: wrap;
			.bk-legend-item {
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				margin:0 12px 4px 0;
				&:before {
					display:none;
				}
				.bk-lg-icon {
					width:24px;
					height:24px;
					background:#32373c;
					margin-right:4px;
					i {    
						margin: 7px 6px;
					    display: block;
					    color:$w;
					}
				}
				.bk-lg-txt {
				    margin: 0px 4px 0 2px;
				    font-weight: bold;
				    color: #333;
				    display: inline-block;
				}
				&.style-icon-today {
					.bk-lg-icon {
						background:var(--bk-selected-bg-color);
						position: relative;
						border: 1px solid #b5b5b5;
						&:after {
							content:'';
							position: absolute;
							top:0;
							left:0;
							width:$p1;
							height:$p1;
							background:rgba(0,0,0,.3);
							display:none;
						}
					}
				}
				&.style-icon-full {
					.bk-lg-icon {
						background:var(--bk-admin-base);
					}
				}
				&.style-icon-half {
					.bk-lg-icon {
						background:var(--bk-admin-base);
						position:relative;
						overflow: hidden;
						i {
							display:block;
							position: absolute;
						    top: 72%;
						    left: 74%;	
							width: 140%;
							height: 140%;
							background:var(--bk-admin-highlight);
							@include transform(translate(-50%,-50%) rotate(-45deg));
						}
					}
				}
				&.style-icon-pending {
					.bk-lg-icon {
						background:var(--bk-admin-pending);
					}
				}
				&.style-icon-pending-half {
					.bk-lg-icon {
						background:var(--bk-admin-pending);
						position:relative;
						overflow: hidden;
						i {
							display:block;
							position: absolute;
						    top: 72%;
						    left: 74%;	
							width: 140%;
							height: 140%;
							background:var(--bk-admin-pending);
							-webkit-filter: brightness(120%);
						    filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' ><filter idâ€¦pe='linear' slope='1.25' /></feComponentTransfer></filter></svg>#bright120");
						    filter: brightness(120%);
							@include transform(translate(-50%,-50%) rotate(-45deg));
						}
					}
				}
			}
		}
	}
	#bk-form {
		margin:40px 0 80px;
		width:$p1;
		max-width:$p1;
		font-size:0;
		h1 {
			font-size: 20px;
		    font-family: inherit;
		    margin: 0 0 14px;
		    padding: 16px 20px;
			font-weight: bold;
			background:var(--bk-admin-base);
			color:$w;
			position: relative;
			width:$p1;
			clear: none;
			@include box-sizing(border-box);
			&:before {
				display:none;
			}
			span {
				position: absolute;
				right:24px;
				top:$p2;
				font-size:16px;
				@include transform(translateY(-$p2));
			}
		}
		h2 {
			font-size:18px;
			font-family: inherit;
			margin:0 0 40px;
			line-height: 1.6;
			padding:0;
			font-weight: 400;
			clear: none;
			width:$p1;
			color:#1b1b1b!important;
			// max-width:calc(66.66666% - 20px);
			&:before {
				display:none;
			}
		}
		h3 {
			font-size:14px;
		}
		.bk-form-col {
			width:$p3;
			padding-right:20px;
			vertical-align: top;
			display:inline-block;
			.bk-form-group {
				width:$p1;
				padding-right:0;
			}
		}
		.bk-form-group {
			display:inline-block;
			width:$p3;
			font-size:16px;
			padding-right:20px;
			margin-bottom:0;
			&.bk-form-file,
			&.bk-form-textarea {
				// width:$p2;
				vertical-align: top;
			}
			&.bk-form-file {
				.bk-file-wrap {
					position:relative;
					cursor: pointer;
				    display: block;
				    width: $p1;
				    height:130px;
				    padding: 0;
				    margin: 0;
				    border-radius: 0;
				    background: #f8f8fa;
				    padding: 6px 10px;
				    font-family: inherit;
				    outline: 0 !important;
				    border: 1px solid #c8c8c8;
				}
				input {
					opacity:0;
					position: absolute;
					top:0;
					left:0;
					width:$p1;
					height:$p1!important;
					z-index:9;
					cursor: pointer;
				}
				p.bk-file-text {
					position: absolute;
					top:$p2;
					left:$p2;
					cursor: pointer;
					margin: 0;
					font-weight: bold;
					text-align: center;
					line-height:1.3;
					font-size: 14px;
					width:$p1;
					@include transform(translate(-$p2,-$p2));
					span {
						font-size:12px;
						font-weight: normal;
						font-style:italic;
					}
				}
				.bk-form-status {
				}
			}
			h3 {
				font-weight:bold;
				font-size: 16px;
				margin: 6px 0 14px;
				color:#1b1b1b!important;
			}
			label {
				display:block;
			    width: $p1;
			    line-height: 1.4;
			    font-size: 14px;
			    margin-bottom:8px;
			}
			textarea {
				resize:none;
			}
			input,select,textarea {
				display:block;
				width:$p1;
				@include appearance(none);
				padding:0;
				margin:0;
				color:$b;
				border-radius:0;
				background:#f8f8fa;
				padding:6px 10px;
				font-size:15px;
				font-family: inherit;
				outline:0!important;
				border:1px solid #c8c8c8;
			}
			textarea {
				height:130px;
			}
			input[type="file"]{
				height:130px;
			}
			.bk-select {
				position:relative;
				.bk-select-icon {
					position:absolute;
					right:10px;
					top:$p2;
					@include transform(translateY(-$p2));
					width:16px;
					height:16px;
					svg {
						position: absolute;
					    top: $p2;
						@include transform(translateY(-$p2));
					}
				}
			}
			.bk-form-status {
				opacity: 0;
				visibility: hidden;
				p {
					margin:0 0 8px;
				    font-size: 10px;
				    font-weight:bold;
				    color: #c03;
				}
			}
			&.bk-status-is {
				.bk-form-status {
					opacity: 1;
					visibility: visible;
				}
			}
		}
		hr {
			margin: 10px 0 20px!important;
		    background: #dadada;
		    max-width: 100%;
		    height: 1px;
		    border:0;
		}
		input[type="submit"]{
			margin-top:10px;
		    font-size: 15px;
		    padding: 12px 22px;
		    border-radius: 3px;
		    font-weight:bold;
		    color:$w;
		    outline:0!important;
		    box-shadow:none!important;
		    &[disabled]:not(.processing){
		    	// background:#d9d9d9;
		    	// color:#9e9e9e;
		    	color:#ff9bb4;
		    	background:#c03;
		    }
		}
		.bk-submit-info {
			font-size:10px;
			font-weight:bold;
			color:#c03;
			display:none;
			line-height: normal;
			margin-top:10px;
			margin-bottom:30px;
			&.in {
				display:block;
			}
		}
		.hidden {
			display:none;
		}
	}
}
		

@media (max-width:920px){
	#bk-med-wrapper #bk-form .bk-form-col,
	#bk-med-wrapper #bk-form .bk-form-group {
		width:$p2;
	}
	#bk-med-wrapper #bk-calendar.bk-hasside {
		max-width:$p1;
	}
	#bk-med-wrapper #bk-side-notice {
		width:$p1;
		float:none;
		margin-right:0;
		margin-top:10px;
	}
	#bk-med-wrapper #bk-form {
		margin-top:10px;
	}
}

@media (max-width:600px){
	#bk-med-wrapper .bk-med-status {
		font-size:12px;
		padding:2px 0px 1px;
	}
	#bk-med-wrapper {
		overflow:hidden;
	}
	#bk-med-wrapper #bk-calendar .bk-cal {
		width:$p1;
	}
	#bk-med-wrapper #bk-calendar .bk-cal:not(.bk-cal-prev) {
		display:none;
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-week span {
		padding:0;
		font-size:9px;
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-btn.bk-mobi-next {
		display:block;
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-date {
		padding:9px 0;
	}
	#bk-med-wrapper #bk-calendar .bk-selection {
		margin:0;
	}
	#bk-med-wrapper #bk-form {
		margin:20px 0 10px;
	}
	#bk-med-wrapper #bk-form h1 {
		font-size:14px;
		padding:12px 12px;
		span {
			right:14px;
			font-size:12px;
		}
	}
	#bk-med-wrapper #bk-form h2 {
		margin:0 0 20px;
		font-size:14px;
	}
	#bk-med-wrapper #bk-form .bk-form-col,
	#bk-med-wrapper #bk-form .bk-form-group {
		width:$p1;
		padding-right:0;
	}
	#bk-med-wrapper #bk-form .bk-form-group .bk-form-status p {
		margin:0;
	}
	#bk-med-wrapper #bk-form input[type="submit"] {
	    width: $p1;
	    font-size: 13px;
	    padding: 8px 22px 6px;
	}
	#bk-med-wrapper #bk-side-notice {
		padding:14px;
		font-size:12px;
	}
	#bk-med-wrapper #bk-side-notice h1 {
		font-size:14px;
	}
	#bk-med-wrapper #bk-calendar .bk-legend {
		margin-top:14px;
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-date.haspop {
		position:static;
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-date .bk-popup {
		bottom:auto;
		left:$p2;
		top:$p2;
		@include transform(translate(-$p2,-$p2));
		z-index:9999;
		@include transition(none);
	}
	#bk-med-wrapper #bk-calendar .bk-cal .bk-date--selected:after {
		z-index:9998;
	}
}







